// debug
namespace Debug {

  // debug
  let DEBUG: boolean = true;

  /**
   * log
   * @param {...any[]} args
   */
  export function log(...args: any[]) {
    if(!DEBUG) {
      return;
    }
    console.log(`[Leaf.Book]`, ...args);
  }

  /**
   * error
   * @param {...any[]} args
   */
  export function error(...args: any[]) {
    if(!DEBUG) {
      return;
    }
    console.error(`[Leaf.Book]`, ...args);
  }
}


export {
  Debug as default,
  Debug
};