import {
  createAsyncThunk,
  createSlice,
} from "@reduxjs/toolkit";
import Debug from "@Shared/utils/debug";
import { Book, type BookProfile } from "@Shared/booksources";
import { RootState } from "..";
import { Toast } from '@Client/components/toast';


export interface SearchState {
  bookList: BookProfile[];
  selected?: BookProfile;
}

const initialState: SearchState = { bookList: []};


// 搜索书籍列表
export const searchBook = createAsyncThunk(
  'book/search',
  async (searchKey: string, {getState}) => {
    const {booksource} = getState() as RootState;
    if(booksource.length === 0) {
      return [];
    }
    const bookInfos = await Book.search(searchKey, booksource[1]);
    return bookInfos;
  }
);

// search sliece
export const searchSlice = createSlice({
  name: 'BookSearch' as const,
  initialState,
  reducers: {
    reset: () => initialState
  },
  extraReducers: builder => {
    builder
      .addCase(
        searchBook.pending,
        _ => {
          Toast.show({icon: 'loading', content: '搜索书籍...', maskClickable: false, duration: 10000});
        }
      )
      .addCase(
        searchBook.fulfilled,
        (state, {payload}) => {
          state.bookList = payload;
          Debug.log('搜索书籍成功!');
          Toast.clear();
          return state;
        }
      )
      .addCase(
        searchBook.rejected,
        (_, err) => {
          Toast.clear();
          Toast.show('搜索书籍失败！')
        }
      );
  }
});


export const searchSelector = (state: RootState) => state.search;
export const { reset } = searchSlice.actions;
export default searchSlice.reducer;