import {
  type RuleSegment,
  type Rule,
  analyze
} from "./selector.analyzer";

/**
 * @description 根据规则获取属性
 * @param {Element} dataSource  数据源
 * @param {Rule} rule 规则
 * @return {*}  {string}
 */
export function getAttribute(dataSource: Element, rule: Rule): string;

 /**
  * @description 解析规则文本并根据规则获取属性
  * @param {Element} dataSource  数据源
  * @param {string} rule 规则文本
  * @return {*}  {string}
  */
export function getAttribute(dataSource: Element, rule: string): string;
export function getAttribute(dataSource: Element, rule: Rule | string): string {
  rule = typeof rule === 'string' ? analyze(rule) : rule;
  const { segments } = rule;
  for (const segment of segments) {
    const attribute = _getAttribute(dataSource, segment);
    if(attribute !== null) {
      return attribute;
    }
  }
  return '';
}

/**
 * @description 根据规则块获取属性
 * @param {Element} dataSource
 * @param {RuleSegment} { selector }
 * @return {*}  {string}
 */
function _getAttribute(dataSource: Element, {
  selector,
  attribute,
  replaceRegexp,
  replacement
}: RuleSegment): string {
  const element = dataSource.querySelector(selector);
  if(element === null) {
    return '';
  }

  if(attribute === undefined) {
    return element.textContent ?? '';
  }

  const attributeValue = element[attribute as keyof Element]?.toString();
  if(attributeValue === undefined) {
    return '';
  }

  if(replaceRegexp !== undefined) {
    return attributeValue.replace(replaceRegexp, replacement ?? '');
  }
  return attributeValue;
}