import { FC } from "react";

export namespace DarkMode {
  // dark calss
  const DarkClass = 'dark';

  // on switch
  export function onSwitch(dark?: boolean): void {
    const flag = isDark();
    if(flag === dark) {
      return;
    }
    dark ??= !flag;
    if(!dark) {
      document.documentElement.classList.remove(DarkClass);
      return;
    }
    document.documentElement.classList.add(DarkClass);
  }

  // is dark mode
  export function isDark(): boolean {
    return document.documentElement.classList.contains(DarkClass);
  }

  // dark 模式触发器
  export const Trigger: FC = () => {
    return (
      <div className='btn absolute right-0 top-0 p-0 opacity-30' onClick={() => DarkMode.onSwitch()}>@</div>
    );
  };
}

export default DarkMode;