import { configureStore } from "@reduxjs/toolkit";
import {
  TypedUseSelectorHook,
  useDispatch,
  useSelector
} from "react-redux";
import { bookSlice } from "./features/slice.book";
import { bookSettingsSlice } from "./features/slice.book.settings";
import { bookshelfSlice } from "./features/slice.bookshelf";
import { bookSourceSlice } from "./features/slice.booksource";
import { searchSlice } from "./features/slice.search";

// store
const store = configureStore({
  reducer: {
    booksource: bookSourceSlice.reducer,
    search: searchSlice.reducer,
    book: bookSlice.reducer,
    booksettings: bookSettingsSlice.reducer,
    bookshelf: bookshelfSlice.reducer
  }
});

// root state
export type RootState = ReturnType<typeof store.getState>;

// dispath
type AppDispath = typeof store.dispatch;

// // instead `useDispatch`
export const useAppDispatch: () => AppDispath = useDispatch;

// instead `useSelector`
export const useAppSelector: TypedUseSelectorHook<RootState> = useSelector;

export default store;