import Debug from "../../utils/debug";

/** UrlAnalyzer */
export namespace UrlAnalyzer {

  // 额外data
  type ExtraData = {
    // 请求方式
    method?: 'POST' | 'GET';
    // 参数字符串:searchkey={{key}}&page={{page}}
    body?: string;
    // response返回数据编码格式
    charset?: string;
  };

  // 解析结果
  type URLResult = {
    // url
    url: string;
    // 请求方式
    method: 'POST' | 'GET';
    // params, only applicable for request methods 'GET'
    params?: Record<string, string>;
    // data, only applicable for request methods 'PUT', 'POST', 'DELETE , and 'PATCH'
    data?: Record<string, string>;
  };

  /**
   * @description 解析url
   * @param {string} rawURL 原始url
   * @return {*}  {URLResult}
   */
  export function analyze(rawURL: string): URLResult {
    const splitArray = rawURL.split(',');
    const urlSplit = splitArray[0].split('?');
    const url = urlSplit[0];
    const paramStr = urlSplit[1] as string | undefined;
    const params = paramStr === undefined ? undefined : queryString(paramStr);
    // 解析额外参数
    let extra: ExtraData | undefined = undefined;
    if(splitArray.length > 1) {
      const extraExecArray = /(\{[^{}]+\})/i.exec(splitArray[1]);
      const extraStr = extraExecArray?.[1];
      if(extraStr !== undefined) {
        try {
          extra = JSON.parse(extraStr);
        } catch (error) {
          Debug.error('url额外参数解析错误！extra=' + extraStr);
        }
      }
    }
    const method = extra?.method ?? 'GET';
    const data = extra?.body === undefined ? undefined : queryString(extra.body);
    return {
      url,
      method,
      params,
      data
    };
  }

  /**
   * @description query string
   * @param {string} str
   * @return {*}  {(Record<string, string> | undefined)}
   */
  function queryString(str: string): Record<string, any> | undefined {
    let result: Record<string, string> | undefined  = undefined;
    str.split('&').forEach(value => {
      const [k, v] = value.split('=');
      if(v !== undefined) {
        result ??= {};
        result[k] = v;
      }
    });
    return result;
  }
}