import { getAttribute, reflect, reflectList } from "../analyzers/css-selectors";
import { BookDataSource } from "../BookDataSource";

/**
 * @descriptin 章节
 */
export interface Chapter {
  // 章节名称
  readonly name?: string;
  // 章节地址
  readonly url?: string;
}

/** Chapter */
export namespace Chapter {

  /**
   * @description 章节规则
   * @extends {Chapter}
   */
  export interface Rule extends Chapter {
    // 章节列表规则
    readonly chapterList?: string;
    // 章节目录列表分页，下一页地址
    readonly nextPageUrl?: string;
  }

  /**
   * @description 解析数据源获取章节
   * @param {Element} source  章节数据源
   * @param {Rule} rule 章节规则
   * @return {*}  {Chapter}
   */
  export function getChapter(source: Element, rule: Rule): Chapter {
    return reflect(source, rule, ['chapterList', 'nextPageUrl']);
  }

  /**
   * @description 解析数据源获取章节列表
   * - 仅解析当前数据源内的章节列表，并不对分页列表(`nextPageUrl`)进行处理
   * @param {Element} source 章节数据源
   * @param {Rule} rule 章节规则
   * @return {*}  {Chapter[]}
   */
  export function getChapterList(source: Element, rule: Rule): Chapter[] {
    const { chapterList } = rule;
    if(chapterList === undefined) {
      return [];
    }
    return reflectList(source, chapterList, rule, ['chapterList', 'nextPageUrl']);
  }

  /**
   * @description 获取章节列表
   * - 如果有章节列表分页，依次请求每页章节列表，合并成完整章节列表
   * @param {string} chapterListUrl 章节列表地址
   * @param {Rule} rule 章节规则
   * @return {*}  {Promise<Chapter[]>}
   */
  export function getChapterListAwait(chapterListUrl: string, rule: Rule): Promise<Chapter[]>;

  /**
   * @description 获取章节列表
   * - 如果有章节列表分页，依次请求每页章节列表，合并成完整章节列表
   * @param {Element} source 章节数据源
   * @param {Rule} rule 章节规则
   * @return {*}  {Promise<Chapter[]>}
   */
  export function getChapterListAwait(source: Element, rule: Rule): Promise<Chapter[]>;

  export function getChapterListAwait(data: Element | string, rule: Rule): Promise<Chapter[]>;
  export function getChapterListAwait(data: Element | string, rule: Rule): Promise<Chapter[]> {
    if(rule.chapterList === undefined) {
      return Promise.resolve([]);
    }

    // 根据地址获取章节列表
    if(typeof data === 'string') {
      const result = BookDataSource.request(data).then(source => getChapterListAwait(source, rule));
      return result;
    }

    // 根据数据源获取章节列表
    const source = data;
    const list = getChapterList(source, rule);
    if(list.length === 0) {
      // 当前列表为空，不会继续请求分页列表
      return Promise.resolve(list);
    }

    // 获取分页地址
    let { nextPageUrl } = rule;
    nextPageUrl = nextPageUrl === undefined ? '' : getAttribute(source, nextPageUrl);
    if(nextPageUrl.length === 0) {
      return Promise.resolve(list);
    }

    // 合并分页章节列表
    const result = getChapterListAwait(nextPageUrl, rule).then(nextList => list.concat(nextList));
    return result;
  }
}




