/** ------------ Chapter Content -------------- */
/** ------------ Chapter Content -------------- */
export namespace Chapter {

  /**
   * @description 章节内容规则
   */
  export interface ContentRule {
    // 正文
    readonly content?: string;
    // 正文下一页地址，分页正文
    readonly nextContentUrl?: string;
  }

  /**
   * @description 从数据源获取正文内容
   * - 只获取当前页面的正文内容，忽略分页正文
   * @param {Element} source 章节正文内容数据源
   * @param {ContentRule} rule 正文规则
   * @return {*}  {Promise<string>}
   */
  export function getContent(source: Element, rule: ContentRule): string {
    return reflect(source, rule, ['nextContentUrl']).content ?? '';
  }

  /**
   * @description 获取正文内容
   * - 如果正文分页, 依次请求分页内容，合并成完整正文
   * @param {string} contentUrl 章节正文内容地址
   * @param {ContentRule} rule  章节正文规则
   * @return {*}  {Promise<string>}
   */
  export function getContentAwait(contentUrl: string, rule: ContentRule): Promise<string>;

  /**
   * @description 获取正文内容
   * - 如果正文分页, 依次请求分页内容，合并成完整正文
   * @param {Element} source 章节正文内容数据源
   * @param {ContentRule} rule 章节正文规则
   * @return {*}  {Promise<string>}
   */
  export function getContentAwait(source: Element, rule: ContentRule): Promise<string>;

  export function getContentAwait(data: Element | string, rule: ContentRule): Promise<string> {
    // 根据地址获取正文内容
    if(typeof data === 'string') {
      const result = BookDataSource.request(data).then(source => getContentAwait(source, rule));
      return result;
    }

    // 解析数据源中的正文内容
    const source = data;
    const { content, nextContentUrl} = reflect(source, rule);

    // 合并分页正文
    if(nextContentUrl === undefined || !content?.length) {
      // 没有分页或当前正文内容为空，不会继续请求分页内容
      return Promise.resolve(content ?? '');
    }
    const result = getContentAwait(nextContentUrl, rule).then(nextContent => `${content}\n${nextContent}`);
    return result;
  }
}