import Debug from "../../utils/debug";
import { BookDataSource } from "../BookDataSource";
import { BookInfo } from "./BookInfo";
import { Chapter } from "./Chapter";
import { Search } from "./Search";
import type { BookSource } from "../BookSource";

/**
 * @description BookProfile
 */
export interface BookProfile extends BookInfo {
  // 书籍地址
  readonly bookUrl: string;
  // 书源
  readonly bookSource: BookSource;
}

/**
 * @description Book
 */
export interface Book extends BookProfile {
  // 章节列表
  readonly chapters: Chapter[];
}

/** Book */
export namespace Book {

  /**
   * @description 加载书籍
   * @param {BookProfile} bookProfile
   * @return {*}  {Promise<Book>}
   */
  export function load(bookProfile: BookProfile): Promise<Book> {
    // 书籍地址
    const bookUrl = bookProfile.bookUrl;

    // 地址为空
    if(bookUrl.length === 0) {
      const reason = '无效书籍地址! url=' + bookUrl;
      Debug.error(reason);
      return Promise.reject(reason);
    }


    const { bookInfoRule, chapterRule } = bookProfile.bookSource;

    // 检测规则
    if(bookInfoRule === undefined) {
      const reason = '书籍详情规则格式错误！';
      Debug.error(reason);
      return Promise.reject(reason);
    }

    return BookDataSource.request(bookUrl).then(
      source => {
        // 解析书籍信息
        const bookInfo = BookInfo.getBookInfo(source, bookInfoRule);
        // 检测章节列表规则
        if(chapterRule === undefined) {
          const book: Book = { ...bookProfile, ...bookInfo, chapters: [] };
          return book;
        }

        // 解析章节列表
        const { chapteListsUrl } = bookInfo;
        const chapterData = chapteListsUrl?.length? chapteListsUrl : source;
        const chaptersAwait = Chapter.getChapterListAwait(chapterData, chapterRule);
        const bookAwait = chaptersAwait.then(chapters => {
          const book: Book = { ...bookProfile, ...bookInfo, chapters};
          return book;
        });
        return bookAwait;
      }
    );
  }

  /**
   * @description 加载书籍章节内容
   * @param {Book} book 书籍
   * @param {number} chapterIndex 章节索引
   * @return {*}  {Promise<string>}
   */
  export function loadChapterContent(book: Book, chapterIndex: number): Promise<string> {
    // 章节内容规则检测
    const { contentRule } = book.bookSource;
    if(contentRule === undefined) {
      const reason = `章节规则格式错误!`;
      Debug.error(reason);
      return Promise.reject(reason);
    }

    const { chapters } = book;
    const chapterExist = chapterIndex < chapters.length && chapterIndex > -1;
    if(chapterExist === false) {
      const reason = `超出章节列表索引范围！总章节：${chapters.length}, 请求章节索引：${chapterIndex}`;
      Debug.error(reason);
      return Promise.reject(reason);
    }

    const { url, name } = chapters[chapterIndex];
    if(!url?.length) {
      const reason = `章节【${name}】地址不存在！`;
      Debug.error(reason);
      return Promise.reject(reason);
    }
    return Chapter.getContentAwait(url, contentRule);
  }

  /**
   * @description 搜索书源内的书籍
   * @param {string} bookName 书名
   * @param {true} bookSource 书源
   * @return {*}  {Promise<BookBase[]>}
   */
  export function search(bookName: string, bookSource: BookSource): Promise<BookProfile[]>;

  /**
   * @description 搜索书源列表内的书籍
   * - 只要书源列表中的任意一个书源搜索结果不为空，返回搜索结果
   * @param {string} bookName 书名
   * @param {BookSource[]} bookSourceList 书源列表
   * @return {*}  {Promise<BookBase[]>}
   */
  export function search(bookName: string, bookSourceList: BookSource[]): Promise<BookProfile[]>;

  /**
   * @description 搜索书源列表内的书籍
   * - 搜索所有书源，返回合并后的结果
   * @param {string} bookName 书名
   * @param {BookSource[]} bookSourceList 书源列表
   * @param {true} merge 合并后的结果
   * @return {*}  {Promise<BookBase[]>}
   */
  export function search(bookName: string, bookSourceList: BookSource[], merge: true): Promise<BookProfile[]>;
  export function search(bookName: string, bookSources: BookSource | BookSource[], merge?: true): Promise<BookProfile[]> {
    let bookSourceList = Array.isArray(bookSources) ? bookSources : [bookSources];
    if(bookSourceList.length === 0) {
      return Promise.resolve([]);
    }

    const bookSource = bookSourceList[0];
    [, ...bookSourceList] = bookSourceList;

    const { searchUrl, searchRule } = bookSource;
    if(searchUrl === undefined || searchRule === undefined) {
      if(bookSourceList.length > 0) {
        // 查询下一个书源
        return search(bookName, bookSourceList);
      }
      return Promise.resolve([]);
    }

    // 搜索当前书源
    const url = searchUrl.replace('{{key}}', bookName).replace('{{page}}', '1');
    let baseInfoList = Search.getBookInfoListAwait(url, searchRule).then(books => {
      const list = books
        .filter(bookInfo => bookInfo.bookUrl !== undefined)
        .map(bookInfo => ({...bookInfo, bookSource})) as BookProfile[];
      return list;
    });

    // 书源列表为空，返回结果
    if(bookSourceList.length === 0) {
      return baseInfoList;
    }

    // 继续搜索下一个书源，合并搜索结果
    if(merge === true) {
      baseInfoList = baseInfoList.then(list => {
        return search(bookName, bookSourceList, merge).then(nextList => list.concat(...nextList));
      });
      return baseInfoList;
    }

    // 如果当前书源搜索结果不为空，返回结果，否则继续搜素下一个书源
    baseInfoList = baseInfoList.then(list => {
      return list.length > 0 ? list : search(bookName, bookSourceList);
    });
    return baseInfoList;
  }
}