import { BookAPI } from "../apis";
import Debug from "../utils/debug";
import { UrlAnalyzer } from "./analyzers";

/** BookDataSource */
export namespace BookDataSource {

  /**
   * @description axios 请求
   *  - 用来设置其他`axios`请求
   */
  export let axiosRequest = BookAPI.request;

  /**
   * @description `dom`解析器
   */
  const domParser = new DOMParser();

  /**
   * @description 请求数据
   * @param {string} url
   * @return {*}  {Promise<Element>}
   */
  export function request(url: string): Promise<Element> {
    const config = UrlAnalyzer.analyze(url);
    const response = axiosRequest(config);
    response.catch(reason => {
      Debug.error(reason);
    });
    const result = response.then(({ data }) => {
      return domParser.parseFromString(data, 'text/html').body
    })
    return result;
  }

}