import {
  createAsyncThunk,
  createSlice,
  PayloadAction,
} from "@reduxjs/toolkit";
import Debug from "@Shared/utils/debug";
import { BookShelf } from '@Shared/bookshelf';
import { RootState } from "..";
import { Toast } from '@Client/components/toast';
import type { Book } from "@Shared/booksources";

// 书架state
export interface BookshelfState {
  books: BookShelf.Item[]
}
const initialState: BookshelfState = {books: []};

// compare
const compare = (a: Pick<BookShelf.Item, 'bookUrl'>, b: Pick<BookShelf.Item, 'bookUrl'>): boolean => a.bookUrl === b.bookUrl;

// 加载书架
export const loadBookshelf = createAsyncThunk(
  'book/loadBookshelf',
  BookShelf.load
);

// bookshelf sliece
export const bookshelfSlice = createSlice({
  name: 'BookInfo' as const,
  initialState,
  reducers: {
    // 新增书籍，如果已存在则替换
    add: (state, {payload}: PayloadAction<{book: Book, chapterIndex: number}>) => {
      const { book, chapterIndex } = payload;
      const newItem: BookShelf.Item = {
        bookUrl: book.bookUrl,
        name: book.name ?? '',
        bookSource: book.bookSource.url ?? '',
        coverUrl: book.coverUrl,
        chapterIndex: chapterIndex,
        chapterLength: book.chapters.length ?? 0,
      };

      const idx = state.books.findIndex(value => compare(value, newItem));
      if(idx === -1) {
        state.books.push(newItem);
        Toast.show('已加入书架！')
      }
      else {
        state.books.splice(idx, 1, newItem);
      }
      BookShelf.save([...state.books]);
      return state;
    },
    // 移除书籍
    remove: (state, {payload}: PayloadAction<BookShelf.Item[]>) => {
      Debug.log('书架删除:' + payload);
      state.books = state.books.filter(value => payload.find(deleted => compare(value, deleted)) === undefined);
      BookShelf.save([...state.books]);
      return state;
    },
    reset: () => {
      BookShelf.save([...initialState.books]);
      return initialState;
    }
  },
  extraReducers: builder => {
    builder
      .addCase(
        loadBookshelf.pending,
        state => {
          Toast.show({icon: 'loading', content: '加载书架...', maskClickable: false, duration: 5000});
        }
      )
      .addCase(
        loadBookshelf.fulfilled,
        (state, {payload}) => {
          state.books = payload;
          Debug.log('加载书架成功!');
          Toast.clear();
          return state;
        }
      )
      .addCase(
        loadBookshelf.rejected,
        (_, err) => {
          Toast.clear();
          Toast.show('加载书架失败!')
        }
      );
  }
});


export const bookShelfSelector = (state: RootState) => state.bookshelf;
export const { reset, add, remove } = bookshelfSlice.actions;
export default bookshelfSlice.reducer;