import Debug from "../../../utils/debug";
import {
  type RuleSegment,
  type Rule,
  analyze,
  GroupKind
} from "./selector.analyzer";

/**
 * @description 根据规则获取`Element`
 * @param {Element} dataSource  数据源
 * @param {Rule} rule 规则
 * @return {*}  {Element | null}
 */
export function getElement(dataSource: Element, rule: Rule): Element | null;

 /**
  * @description 解析规则文本并根据规则获取`Element`
  * @param {Element} dataSource  数据源
  * @param {string} rule 规则文本
  * @return {*}  {Element | null}
  */
export function getElement(dataSource: Element, rule: string): Element | null;
export function getElement(dataSource: Element, rule: Rule | string): Element | null {
  rule = typeof rule === 'string' ? analyze(rule) : rule;
  const { segments } = rule;
  for (const segment of segments) {
    const element = _getElement(dataSource, segment);
    if(element !== null) {
      return element;
    }
  }
  return null;
}
/**
 * @description 根据规则获取`Element`列表
 * @param {Element} dataSource  数据源
 * @param {Rule} rule 规则
 * @return {*}  {Element[]}
 */
export function getElements(dataSource: Element, rule: Rule): Element[];

/**
 * @description 解析规则文本并根据规则获取`Element`列表
 * @param {Element} dataSource  数据源
 * @param {string} rule 规则文本
 * @return {*}  {Element[]}
 */
export function getElements(dataSource: Element, rule: string): Element[];
export function getElements(dataSource: Element, rule: Rule | string): Element[] {
  rule = typeof rule === 'string' ? analyze(rule) : rule;
  const { kind, segments } = rule;

  if(kind === GroupKind.Merge) {
    return segments.flatMap(segment => _getElements(dataSource, segment));
  }

  if(kind === GroupKind.Loop) {
    Debug.error(`暂时不能解'Loop'类型规则![rule=${rule.raw}]`);
  }

  // 默认列表行为
  for (const segment of segments) {
    const elements = _getElements(dataSource, segment);
    if(elements.length > 0) {
      return elements;
    }
  }
  return [];
}


/**
 * @description 根据规则块获取`Element`
 * @param {Element} dataSource
 * @param {RuleSegment} { selector }
 * @return {*}  {Element | null}
 */
function _getElement(dataSource: Element, { selector }: RuleSegment): Element | null {
  return dataSource.querySelector(selector);
}

/**
 * @description 根据规则块获取`Element`列表
 * @param {Element} dataSource
 * @param {RuleSegment} { selector, reverse }
 * @return {*}  {Element[]}
 */
function _getElements(dataSource: Element, { selector, reverse }: RuleSegment): Element[] {
  const elements: Element[] = [];
  dataSource.querySelectorAll(selector).forEach(element => elements.push(element));
  if(reverse && elements.length > 0) {
    return elements.reverse();
  }
  return elements;
}
