import { FC, Fragment, ReactNode, useMemo } from "react";
import Mask from "../mask";

export interface ToastProps {
  content?: ReactNode;
  icon?: ReactNode;
  maskClickable?: boolean;
  duration?: number;
  position?: 'top' | 'bottom' | 'center';
}

export const Toast: FC<ToastProps> = p => {
  const props = Object.assign({}, p);
  const { position, content } = props;

  const positionStyle = useMemo(() => {
    switch(position) {
      case 'top':
        return 'top-1/4'
      case 'bottom':
        return 'top-3/4'
      default:
      return 'top-1/2';
    }
  }, [position]);

  return (
    <Fragment>
      <Mask opacity={0} />
      <div className={`fixed flex justify-center w-full ${positionStyle}`}>
        <span className={`bg-gray-600 rounded py-2 px-4 text-white text-base`}>
          {content}
        </span>
      </div>
    </Fragment>
  );
};