import { DarkMode } from "@Client/components/utils/dark-mode";
import { RoutePath } from "@Client/routers";
import { FC } from "react"
import { Navigate, useOutlet } from "react-router-dom"

// home page
const HomePage: FC = () => {
  const outlet = useOutlet();
  return (
    <div className='container mx-auto h-screen flex flex-col bg-leaf-primary dark:bg-leaf-primary-dark'>
      { outlet ?? <Navigate to={RoutePath.BookShelf} /> }
      <DarkMode.Trigger />
    </div>
  )
};
export default HomePage;