import axios, { AxiosRequestConfig } from "axios";

/** Book API */
export namespace BookAPI {

  /**
   * @description 请求代理 - 解决跨域
   * @param {AxiosRequestConfig} data
   * @return {*}
   */
  export function request(data: AxiosRequestConfig) {
    return axios.post<string>('book/request', data, {
      baseURL: '/api',
      timeout: 5000,
    });
  }

}

