import {
  createAsyncThunk,
  createSlice,
} from "@reduxjs/toolkit";
import { type BookSource } from "@Shared/booksources";
import Debug from "@Shared/utils/debug";
import { Toast } from '@Client/components/toast';
import axios from "axios";
import { RootState } from "..";


const initialState: BookSource[] = [];


// load book source list
export const loadBookSourceList = createAsyncThunk(
  'booksource/load',
  async (_, { getState }) => {
    const { booksource } = getState() as RootState;
    if(booksource.length > 0) {
      return booksource;
    }
    const { data } = await axios.get<BookSource[]>('/js/booksource.configs.json', {baseURL: '/web', timeout: 5000});
    return data;
  }
);

// booksource sliece
export const bookSourceSlice = createSlice({
  name: 'BookSource' as const,
  initialState,
  reducers: {
  },
  extraReducers: builder => {
    builder
      .addCase(
        loadBookSourceList.pending,
        _ => {
          Toast.show({icon: 'loading', content: '加载书源列表...', maskClickable: false, duration: 5000});
        }
      )
      .addCase(
        loadBookSourceList.fulfilled,
        (state, {payload}) => {
          state = payload;
          Debug.log('加载书源列表成功!');
          Toast.clear();
          return state;
        }
      )
      .addCase(
        loadBookSourceList.rejected,
        (_, err) => {
          Toast.clear();
          Toast.show({content: '加载书源列表失败!'})
        }
      );
  }
});
export const bookSourceSelector = (state: RootState) => state.booksource;