import { type RouteObject } from "react-router-dom";
import { lazy, ReactNode, Suspense } from "react";

import HomePage from '@Client/pages/home';
const BookPage = lazy(() => import('../pages/book'));
const BookShelfPage = lazy(() => import('../pages/book-shelf'));
const BookStorePage = lazy(() => import('../pages/book-store'));
const UserPage = lazy(() => import('../pages/user'));
const BookSearchPage = lazy(() => import('../pages/book-search'));
const BookInfoPage = lazy(() => import('../pages/book-info'));
const BookReaderPage = lazy(() => import('../pages/book-reader'));
const NotFound = lazy(() => import('../pages/NotFound'));

const loadComponent = (child: ReactNode) => <Suspense fallback={null}>{child}</Suspense>;


export enum RoutePath {
  Home = '/',
  Book = '/book',
  BookShelf = '/book/shelf',
  BookStore = '/book/store',
  User = '/book/user',
  BookSearch = '/book-search',
  BookInfo = '/book-info',
  BookReader = '/book-reader',
}

// route objects
export const RoutersConfig: RouteObject[] = [
  {
    path: RoutePath.Home,
    element: <HomePage />,
    children: [
      {
        path: RoutePath.Book,
        element: loadComponent(<BookPage/>),
        children: [
          {
            path: RoutePath.BookShelf,
            element: loadComponent(<BookShelfPage/>),
          }
          ,
          {
            path: RoutePath.BookStore,
            element: loadComponent(<BookStorePage/>),
          }
          ,
          {
            path: RoutePath.User,
            element: loadComponent(<UserPage/>),
          }
        ]
      }
      ,
      {
        path: RoutePath.BookSearch,
        element: loadComponent(<BookSearchPage/>),
      }
      ,
      {
        path: RoutePath.BookInfo,
        element: loadComponent(<BookInfoPage/>),
      }
      ,
      {
        path: RoutePath.BookReader,
        element: loadComponent(<BookReaderPage/>),
      }
    ]
  }
  ,
  {
    path: '*',
    element: loadComponent(<NotFound/>)
  }
];