import {
  createAsyncThunk,
  createSlice,
  PayloadAction,
} from "@reduxjs/toolkit";
import Debug from "@Shared/utils/debug";
import { Book, type BookProfile } from "@Shared/booksources";
import { RootState } from "..";
import { Toast } from '@Client/components/toast';


export interface BookState {
  book?: Book;
  chapterIndex: number;
}

const initialState: BookState = { chapterIndex: 0 };


// 加载书籍
export const loadBook = createAsyncThunk(
  'book/load',
  async (bookProfile: BookProfile) => {
    const book = await Book.load(bookProfile);
    return book;
  }
);

// book sliece
export const bookSlice = createSlice({
  name: 'BookInfo' as const,
  initialState,
  reducers: {
    gotoChapter: (state, {payload}: PayloadAction<number>) => {
      Debug.log('设置章节索引:' + payload);
      state.chapterIndex = payload;
      return state;
    },
    download: (state, {payload}: PayloadAction<{startChapterIndex: number, length?: number}>) => {
      Debug.log('下载书籍:' + payload);
      if(state.book === undefined) {
        return state;
      }

      const {startChapterIndex, length} = payload;
      const totalChapterLength = state.book.chapters.length;
      if(startChapterIndex >= totalChapterLength) {
        return state;
      }

      const endIndex =  Math.min(totalChapterLength, startChapterIndex + (length ?? totalChapterLength));
      const contents: string[] = [];
      // 这tm有问题，关于proxy
      for (let idx = startChapterIndex; idx < endIndex; idx++) {
        Book.loadChapterContent(state.book, idx).then(content => {
          contents.push(content);
          console.log(`缓存章节：${idx}, 字数：${content.length}`);

        });
      }
      return state;
    },
    reset: () => initialState
  },
  extraReducers: builder => {
    builder
      .addCase(
        loadBook.pending,
        state => {
          state.chapterIndex = 0;
          Toast.show({icon: 'loading', content: '加载书籍...', maskClickable: false, duration: 5000});
        }
      )
      .addCase(
        loadBook.fulfilled,
        (state, {payload}) => {
          state.book = payload;
          Debug.log('加载书籍成功!');
          Toast.clear();
          return state;
        }
      )
      .addCase(
        loadBook.rejected,
        (_, err) => {
          Toast.clear();
          Toast.show('加载书籍失败!')
        }
      );
  }
});


export const bookSelector = (state: RootState) => state.book;
export const { reset, gotoChapter, download } = bookSlice.actions;
export default bookSlice.reducer;