import type { BookProfile } from "../booksources";

/**
 * @description 书架
 */
export interface BookShelf {
  /**
   * @description 加载书架
   * @return {*}  {Promise<BookShelf.Item[]>}
   */
  load(): Promise<BookShelf.Item[]>;

  /**
   * @description 保存书架
   * @param {BookShelf.Item[]} items
   * @return {*}  {Promise<void>}
   */
  save(items: BookShelf.Item[]): Promise<void>;
}

// 书架
export namespace BookShelf {

  // localStaorage
  const LocalStorageKey = 'leaf.book.bookshelf';

  /**
   * @description BookShelf.Item
   * @extends {Omit<BookProfile, 'bookSource'>}
   */
  export interface Item extends Omit<BookProfile, 'bookSource'> {
    // 书源地址
    readonly bookSource: string;
    // 当前章节索引
    readonly chapterIndex: number;
    // 章数
    readonly chapterLength: number;
  }

  /**
   * @description 加载书架
   * @return {*}  {Promise<Item[]>}
   */
  export function load(): Promise<Item[]> {
    const data = localStorage.getItem(LocalStorageKey);
    const books:Item[] = data === null ? [] : JSON.parse(data);
    return Promise.resolve(books);
  }

  /**
   * @description 保存书架
   * @param {Item[]} items
   * @return {*}  {Promise<void>}
   */
  export function save(items: Item[]): Promise<void> {
    const data = JSON.stringify(items);
    localStorage.setItem(LocalStorageKey, data);
    return Promise.resolve();
  }
}