import { FC, PropsWithChildren } from "react";

const defaultProps: MaskProps = {
  visible: true,
  opacity: 20,
};

export interface MaskProps extends PropsWithChildren {
  // visible
  visible?: boolean;
  // 透明度 0 - 100
  opacity?: number;
  // on mask click
  onMaskClick?: () => void;
}

const Mask: FC<MaskProps> = ({
  visible,
  opacity,
  children
}) => {
  opacity ??= 20;
  opacity *= 0.01;
  return (
    <div className='absolute inset-0 z-40 bg-black select-none' style={{opacity: opacity.toFixed(2)}}>
      {children}
    </div>
  );
};

export default Mask;