import { createElement } from "react";
import { renderToBody } from "../utils/render-to-body";
import { Toast as ToastNode, ToastProps } from "./Toast";

// Toast
export namespace Toast {

  // timeout id
  let currentTimeoutID: number | null = null;

  // toast unmount
  let toastUnmount: (() => void) | null = null;

  // 默认props
  const defaultProps: ToastProps = {
    duration: 2000,
    position: 'center',
    maskClickable: true
  };

  // toast inner
  const ToastInner = (props: ToastProps) => createElement(ToastNode, Object.assign({}, props));

  /**
   * @description 显示文本
   * @param {string} content
   */
  export function show(content: string): void;
  /**
   * @description 根据配置显示文本
   * @param {ToastProps} props
   */
  export function show(props: ToastProps): void;
  export function show(p: string | ToastProps): void {
    if(typeof p === 'string') {
      p = { content : p};
    }
    const props = {...defaultProps, ...p};
    const element = createElement(ToastInner, Object.assign({}, props));
    if(toastUnmount) {
      toastUnmount();
    }
    else {
      toastUnmount = renderToBody(element);
    }

    if(currentTimeoutID) {
      window.clearTimeout(currentTimeoutID)
    }

    if(props.duration) {
      currentTimeoutID = window.setTimeout(() => clear(), props.duration);
    }
  }

  /**
   * @description 清理
   */
  export function clear():void {
    toastUnmount?.();
    toastUnmount = null;
  }
}

export default Toast;

