import {
  createAsyncThunk,
  createSlice,
  PayloadAction,
} from "@reduxjs/toolkit";
import Debug from "@Shared/utils/debug";
import { BookSettings } from '@Shared/settings';
import { RootState } from "..";
import { Toast } from '@Client/components/toast';


const initialState: BookSettings = BookSettings.defaultSettings;


// 加载书籍设置
export const loadBookSettings = createAsyncThunk(
  'book/settings',
  async () => {
    return initialState;
  }
);

// book setttings sliece
export const bookSettingsSlice = createSlice({
  name: 'BookInfo' as const,
  initialState,
  reducers: {
    setBookSettings: (state, {payload}: PayloadAction<Partial<BookSettings>>) => {
      Debug.log('书籍设置:' + payload);
      state = {...state, ...payload};
      BookSettings.save(state);
      return state;
    },
    reset: () => initialState
  },
  extraReducers: builder => {
    builder
      .addCase(
        loadBookSettings.pending,
        state => {
          Toast.show({icon: 'loading', content: '加载书籍设置...', maskClickable: false, duration: 5000});
        }
      )
      .addCase(
        loadBookSettings.fulfilled,
        (state, {payload}) => {
          state = {...state, ...payload};
          Debug.log('加载书籍设置成功!');
          Toast.clear();
          return state;
        }
      )
      .addCase(
        loadBookSettings.rejected,
        (_, err) => {
          Toast.clear();
          Toast.show('加载书籍设置失败!')
        }
      );
  }
});


export const bookSettingsSelector = (state: RootState) => state.booksettings;
export const { reset, setBookSettings } = bookSettingsSlice.actions;
export default bookSettingsSlice.reducer;